<template>
  <div class="container">
    <div class="dishLists" id="dishList">
      <div class="item" v-for="(item, idx) in items" :id="item.idx" :key="item.idx">
        <div class="img">
          <iframe :src="item.videoUrl" v-if="item.video"></iframe>
          <!-- <video :src="item.videoUrl" :poster="require('@/assets/imgs/dish/' + item.imgUrl)" controls preload="metadata" v-if="item.video"></video> -->
          <img :src="require('@/assets/imgs/dish/' + item.imgUrl)" :alt="item.dishName" @error="replaceByDefault" v-if="item.img">
          <div :class="isDone ? 'tit bgBlack' : 'tit bgWhite'">{{ item.dishName }} <span>[ by. <b>{{ item.dishBy }}</b></span>]</div>
          <a :href="item.href" target="_blank" class="ico-recipe">바로가기</a>
        </div>
        <dl class="ingredients">
          <dt>INGREDIENTS</dt>
          <dd>
            <div v-for="ingredient in item.ingredients">
              {{ ingredient.value }}
            </div>
          </dd>
        </dl>
        <dl class="seasonings">
          <dt>SEASOING</dt>
          <dd>
            <div v-for="seasoning in item.seasonings">
              {{ seasoning.value }}
            </div>
          </dd>
        </dl>
        <dl class="preparation">
          <dt>PREPARATION</dt>
          <dd>
            <ol>
              <li v-for="sequence in item.sequences">
                {{ sequence.value }}
              </li>
            </ol>
          </dd>
        </dl>
      </div>
    </div>
  </div>
</template>

<script>
import img from "@/assets/imgs/noImg.svg";
import data from "@/assets/jsons/dishLists.json";

const items = data;

export default {
  methods: {
    replaceByDefault(e) {
      e.target.src = img
    }
  },
  data() {
    return {
      items,
    }
  }, //data
  computed: {
    
  }, //computed
} // export default
</script>
